<template>
  <div class="notice-context-view">
    <header-view :title="title" :routerName="routerName"></header-view>
    <article class="notice-context" v-html="noticeContent"></article>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '',
        noticeContent: '',
        routerName: 'notice'
      }
    },
    created: function () {
      this.title = this.$route.params.noticeTitle
      this.noticeContent = this.$route.params.noticeContent
    }
  }
</script>

<style>
/* 公告内容页 */

.notice-context-view {
  padding-top: .4rem;
  height: 100%;
}

.notice-context {
  height: 100%;
  padding: .1rem;
  overflow: auto;
  text-indent: .28rem;
  -webkit-overflow-scrolling: touch;
}

.notice-context p {
  padding-bottom: .3rem;
}
</style>


